import { createServerClient } from '@supabase/ssr';
import { Database } from '@/types/supabase';
import type { CookieOptions } from '@supabase/ssr';

// Ïø†ÌÇ§ Ïä§ÌÜ†Ïñ¥ Ïù∏ÌÑ∞ÌéòÏù¥Ïä§
export interface CookieStore {
  get: (name: string) => { name: string; value: string } | undefined;
  set: (cookie: { name: string; value: string; [key: string]: any }) => void;
  remove?: (name: string, options?: any) => void;
}

/**
 * pages ÎùºÏö∞ÌÑ∞ÏôÄ app ÎùºÏö∞ÌÑ∞ Î™®ÎëêÏóêÏÑú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎäî ÏÑúÎ≤Ñ Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ± Ìï®Ïàò
 * 
 * @param cookieStore Ïø†ÌÇ§ Ï†ÄÏû•ÏÜå (headers API ÎòêÎäî req/res Í∞ùÏ≤¥)
 * @returns Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏
 */
export function createServerSupabaseClientWithCookies(cookieStore: CookieStore) {
  if (!process.env.NEXT_PUBLIC_SUPABASE_URL) {
    throw new Error('ÌôòÍ≤Ω Î≥ÄÏàò NEXT_PUBLIC_SUPABASE_URLÏù¥ ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
  }
  
  if (!process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY) {
    throw new Error('ÌôòÍ≤Ω Î≥ÄÏàò NEXT_PUBLIC_SUPABASE_ANON_KEYÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
  }

  return createServerClient<Database>(
    process.env.NEXT_PUBLIC_SUPABASE_URL,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY,
    {
      cookies: {
        async get(name: string) {
          const cookie = await Promise.resolve(cookieStore.get(name));
          return cookie?.value;
        },
        async set(name: string, value: string, options: CookieOptions) {
          try {
            await Promise.resolve(cookieStore.set({ name, value, ...options }));
          } catch (error) {
            // Ïø†ÌÇ§ ÏÑ§Ï†ï Ïã§Ìå® Ïãú Î°úÍπÖ (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå)
            if (process.env.NODE_ENV !== 'production') {
              console.warn('ÏÑúÎ≤Ñ Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏: Ïø†ÌÇ§ ÏÑ§Ï†ï Ïã§Ìå®', error);
            }
          }
        },
        async remove(name: string, options: CookieOptions) {
          try {
            if (cookieStore.remove) {
              await Promise.resolve(cookieStore.remove(name, options));
            } else {
              await Promise.resolve(cookieStore.set({ name, value: '', ...options, maxAge: 0 }));
            }
          } catch (error) {
            // Ïø†ÌÇ§ ÏÇ≠Ï†ú Ïã§Ìå® Ïãú Î°úÍπÖ (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå)
            if (process.env.NODE_ENV !== 'production') {
              console.warn('ÏÑúÎ≤Ñ Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏: Ïø†ÌÇ§ ÏÇ≠Ï†ú Ïã§Ìå®', error);
            }
          }
        },
      },
    }
  );
}

// ÌéòÏù¥ÏßÄ ÎùºÏö∞ÌÑ∞Ïö© ÏûÑÏãú Ìï¥Í≤∞Ï±Ö - Îπà Ïø†ÌÇ§ Ïä§ÌÜ†Ïñ¥Î°ú Ïù∏Ï¶ù ÏóÜÎäî ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ±
const emptyCookieStore: CookieStore = {
  get: () => undefined,
  set: () => {}
};

/**
 * App RouterÏö© ÏÑúÎ≤Ñ Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§.
 * Next.jsÏùò cookies() APIÎ•º ÏÇ¨Ïö©ÌïòÏó¨ Ïù∏Ï¶ù Ïø†ÌÇ§Î•º ÏùΩÏäµÎãàÎã§.
 * 
 * @returns Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ïù∏Ïä§ÌÑ¥Ïä§
 */
export async function createServerSupabaseClient() {
  try {
    // App Router ÌôòÍ≤ΩÏóêÏÑúÎßå cookies() API ÏÇ¨Ïö©
    const { cookies } = await import('next/headers');
    const cookieStore = await cookies();
    
    const appRouterCookieStore: CookieStore = {
      get: (name: string) => {
        const cookie = cookieStore.get(name);
        return cookie ? { name: cookie.name, value: cookie.value } : undefined;
      },
      set: (cookie: { name: string; value: string; [key: string]: any }) => {
        try {
          cookieStore.set(cookie.name, cookie.value, cookie);
        } catch (error) {
          // Ïø†ÌÇ§ ÏÑ§Ï†ï Ïã§Ìå® Ïãú Î°úÍπÖ (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå)
          if (process.env.NODE_ENV !== 'production') {
            console.warn('ÏÑúÎ≤Ñ Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏: Ïø†ÌÇ§ ÏÑ§Ï†ï Ïã§Ìå®', error);
          }
        }
      },
      remove: (name: string, options?: any) => {
        try {
          cookieStore.set(name, '', { ...options, maxAge: 0 });
        } catch (error) {
          // Ïø†ÌÇ§ ÏÇ≠Ï†ú Ïã§Ìå® Ïãú Î°úÍπÖ (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå)
          if (process.env.NODE_ENV !== 'production') {
            console.warn('ÏÑúÎ≤Ñ Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏: Ïø†ÌÇ§ ÏÇ≠Ï†ú Ïã§Ìå®', error);
          }
        }
      }
    };
    
    return createServerSupabaseClientWithCookies(appRouterCookieStore);
  } catch (error) {
    // App RouterÍ∞Ä ÏïÑÎãå ÌôòÍ≤ΩÏóêÏÑúÎäî Îπà Ïø†ÌÇ§ Ïä§ÌÜ†Ïñ¥ ÏÇ¨Ïö© (Pages Router Îì±)
    console.warn('‚ö†Ô∏è App RouterÍ∞Ä ÏïÑÎãå ÌôòÍ≤Ω - Îπà Ïø†ÌÇ§ Ïä§ÌÜ†Ïñ¥ ÏÇ¨Ïö©:', error);
    return createServerSupabaseClientWithCookies(emptyCookieStore);
  }
}

/**
 * Pages RouterÏóêÏÑú ÏÇ¨Ïö©Ìï† Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§.
 * req/res Í∞ùÏ≤¥Î•º ÌÜµÌï¥ Ïø†ÌÇ§Î•º Í¥ÄÎ¶¨Ìï©ÎãàÎã§.
 * 
 * @param req Next.js ÏöîÏ≤≠ Í∞ùÏ≤¥
 * @param res Next.js ÏùëÎãµ Í∞ùÏ≤¥
 * @returns Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏
 */
export function createServerSupabaseClientWithRequest(req: any, res: any) {
  // req/res Í∞ùÏ≤¥Î•º ÏÇ¨Ïö©ÌïòÏó¨ Ïø†ÌÇ§ Ï†ÄÏû•ÏÜå ÏÉùÏÑ±
  const cookieStore: CookieStore = {
    get: (name) => {
      const cookies = req.cookies;
      const value = cookies[name];
      return value ? { name, value } : undefined;
    },
    set: (cookie) => {
      const { name, value, maxAge, domain, path, sameSite, secure } = cookie;
      const cookieOptions = {
        maxAge: maxAge || 0,
        domain,
        path: path || '/',
        sameSite: sameSite || 'lax',
        secure: secure || false,
        httpOnly: true
      };
      
      res.setHeader('Set-Cookie', `${name}=${value}; ${Object.entries(cookieOptions)
        .filter(([_, v]) => v !== undefined)
        .map(([k, v]) => {
          if (k === 'httpOnly' || k === 'secure') return v ? k : '';
          if (k === 'maxAge') return v ? `Max-Age=${v}` : '';
          return `${k.charAt(0).toUpperCase() + k.slice(1)}=${v}`;
        })
        .filter(Boolean)
        .join('; ')}`);
    }
  };
  
  return createServerSupabaseClientWithCookies(cookieStore);
}

/**
 * ÏÑúÎ≤Ñ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú ÌòÑÏû¨ Ïù∏Ï¶ù ÏÑ∏ÏÖòÏùÑ Í∞ÄÏ†∏ÏòµÎãàÎã§.
 * ‚ö†Ô∏è ÎÇ¥Î∂ÄÏ†ÅÏúºÎ°ú getUser()Î•º ÏÇ¨Ïö©ÌïòÎ©∞, Îçî Îπ†Î•∏ getServerUser()Î•º ÏßÅÏ†ë ÏÇ¨Ïö©ÌïòÎäî Í≤ÉÏùÑ Í∂åÏû•Ìï©ÎãàÎã§.
 * @returns ÌòÑÏû¨ Ïù∏Ï¶ù ÏÑ∏ÏÖò ÎòêÎäî null (Ìò∏ÌôòÏÑ±ÏùÑ ÏúÑÌï¥ ÏÑ∏ÏÖò ÌòïÌÉúÎ°ú Î∞òÌôò)
 */
export async function getServerSession() {
  const supabase = await createServerSupabaseClient();
  
  // getUser()Î°ú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÌôïÏù∏ (Îçî Îπ†Î¶Ñ)
  const { data: { user }, error } = await supabase.auth.getUser();
  
  if (error || !user) {
    return { data: { session: null }, error };
  }
  
  // Ìò∏ÌôòÏÑ±ÏùÑ ÏúÑÌï¥ Í∞ÑÎã®Ìïú ÏÑ∏ÏÖò Í∞ùÏ≤¥ ÏÉùÏÑ±
  const mockSession = {
    user,
    access_token: 'token-from-cookies',
    refresh_token: null,
    expires_at: null,
    token_type: 'bearer' as const
  };
  
  return { data: { session: mockSession }, error: null };
}

/**
 * ÏÑúÎ≤Ñ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏ÏòµÎãàÎã§.
 * getSession()Î≥¥Îã§ Îπ†Î•¥Í≥† ÏïàÏ†ïÏ†ÅÏûÖÎãàÎã§.
 * @returns ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÎòêÎäî null
 */
export async function getServerUser() {
  try {
    const supabase = await createServerSupabaseClient();
    const { data: { user }, error } = await supabase.auth.getUser();
    
    if (error) {
      // AuthSessionMissingErrorÎäî Ï†ïÏÉÅÏ†ÅÏù∏ Î°úÍ∑∏ÏïÑÏõÉ ÏÉÅÌÉúÏù¥ÎØÄÎ°ú Í≤ΩÍ≥†Îßå ÌëúÏãú
      if (error.message?.includes('Auth session missing')) {
        // Ïù∏Ï¶ù ÏÑ∏ÏÖòÏù¥ ÏóÜÎäî Ï†ïÏÉÅÏ†ÅÏù∏ ÏÉÅÌÉú
        return null;
      }
      console.warn('üîç [Server] getUser Ïò§Î•ò:', error);
      return null;
    }
    
    return user;
  } catch (error) {
    // ÏòàÏÉÅÏπò Î™ªÌïú Ïò§Î•ò Ï≤òÎ¶¨
    console.warn('üîç [Server] getUser ÏòàÏô∏:', error);
    return null;
  }
}

/**
 * Ï£ºÏñ¥ÏßÑ ÏΩúÎ∞±ÏùÑ Ïù∏Ï¶ùÎêú ÏÇ¨Ïö©Ïûê Ïª®ÌÖçÏä§Ìä∏ÏóêÏÑú Ïã§ÌñâÌïòÎäî Ïú†Ìã∏Î¶¨Ìã∞ Ìï®ÏàòÏûÖÎãàÎã§.
 * Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Ïò§Î•òÎ•º Î∞úÏÉùÏãúÌÇµÎãàÎã§.
 * 
 * @example
 * const result = await withAuth(async (userId) => {
 *   // Ïù∏Ï¶ùÎêú ÏÇ¨Ïö©ÏûêÏóê ÎåÄÌïú ÏûëÏóÖ ÏàòÌñâ
 *   return await db.users.findUnique({ where: { id: userId } });
 * });
 * 
 * @param callback Ïù∏Ï¶ùÎêú ÏÇ¨Ïö©Ïûê IDÎ•º Ïù∏ÏûêÎ°ú Î∞õÎäî ÏΩúÎ∞± Ìï®Ïàò
 * @returns ÏΩúÎ∞± Ìï®ÏàòÏùò Í≤∞Í≥º
 * @throws Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Ïò§Î•ò
 */
export async function withAuth<T>(
  callback: (userId: string) => Promise<T>
): Promise<T> {
  const user = await getServerUser();
  
  if (!user) {
    throw new Error('Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌï©ÎãàÎã§');
  }
  
  return callback(user.id);
} 

/**
 * Ï†ïÏ†Å Î†åÎçîÎßÅÏóêÏÑú Í≥µÍ∞ú Îç∞Ïù¥ÌÑ∞ Ï°∞ÌöåÏö© Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏
 * Ïø†ÌÇ§Î•º ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏïÑ Ï†ïÏ†Å Î†åÎçîÎßÅÍ≥º ISRÏóêÏÑú ÏïàÏ†ÑÌïòÍ≤å ÏÇ¨Ïö© Í∞ÄÎä•
 * @returns Ïù∏Ï¶ù ÏóÜÎäî Í≥µÍ∞ú Îç∞Ïù¥ÌÑ∞ Ï†ÑÏö© Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏
 */
export function createPublicSupabaseClient() {
  if (!process.env.NEXT_PUBLIC_SUPABASE_URL) {
    throw new Error('ÌôòÍ≤Ω Î≥ÄÏàò NEXT_PUBLIC_SUPABASE_URLÏù¥ ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
  }
  
  if (!process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY) {
    throw new Error('ÌôòÍ≤Ω Î≥ÄÏàò NEXT_PUBLIC_SUPABASE_ANON_KEYÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
  }

  return createServerClient<Database>(
    process.env.NEXT_PUBLIC_SUPABASE_URL,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY,
    {
      cookies: {
        // Ï†ïÏ†Å Î†åÎçîÎßÅÏùÑ ÏúÑÌïú Îπà Ïø†ÌÇ§ Ìï∏Îì§Îü¨
        async get() { return undefined; },
        async set() { /* no-op */ },
        async remove() { /* no-op */ },
      },
    }
  );
} 